import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
// 让elementui注册的代码参与打包，才能在页面上参与运行并注册插件
import '@/elementUI/index'
// 引入less文件
import '@/assets/global.less'
// 引入vuequill 富文本编辑器
import VueQuillEditor from 'vue-quill-editor'
import 'quill/dist/quill.core.css'
import 'quill/dist/quill.snow.css'
import 'quill/dist/quill.bubble.css'
// 引入dayjs，格式化事件
import dayjs from 'dayjs'
Vue.use(VueQuillEditor) // 默认全局注册
// 基于dayjs封装一个全局函数来格式化事件(任意的组件可以直接使用$formatDate)
Vue.prototype.$formatDate = (dateObj) => {
  // 借助dayjs内置的方法把日期对象格式化成指定的格式，并把格式化好的字符串返回到调用处
  return dayjs(dateObj).format('YYYY-MM-DD HH-mm:ss')
}
Vue.config.productionTip = false
new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')

// 打包相关问题:
// 问题：打开index.html,出现404问题
// 原因：
// 1.默认打开，index.html引入其他打包的文件使用的是 绝对路径
// 地址是以/开头(要找到当前index.html打开时所在服务器的根地址(文件夹))
// 就得确保你的vscode+liveserver插件打开时，vscode根目录得直接是dist文件夹
// 如果把dist文件夹交给后台运维工程师部署到服务器是，dist下内容就得在服务器根目录才行
// 但是服务器一般有多个项目，不会让你用根目录直接放一个项目，会乱
// 解决：webpack让你写一个配置项publicPath(控制index.html引入其他资源前缀地址)
// vue.config.js(脚手架配置文件,webpack配置文件)
// publiPath:默认值 '/' 确保开发环境下，是这个值，因为开发服务器会把所有打包在内存里而且作为服务器的根目录地址，绝对地址
// 生产环境准备上线了，这个时候，就得用相对地址，publicPath; './'
// 路径不以/开头其实就是./开头(默认的)
