import store from '@/store'
import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    redirect: '/home', // 路由重定向
    component: () => import('@/views/layout'), // 路由懒加载
    children: [
      // 侧边栏导航，点击会切换路由地址，路由地址靠数据请求回来铺设上去的
      // 所以路由规则要配合，保持一致
      {
        path: 'home', // 二级路由不用写'/' 他会自动和上面的进行拼接
        component: () => import('@/views/home')
      },
      {
        path: 'user-info',
        component: () => import('@/views/user/userinfo')
      },
      {
        path: 'user-avatar',
        component: () => import('@/views/user/avatar')
      },
      {
        path: 'user-pwd',
        component: () => import('@/views/user/userpwd')
      },
      {
        path: 'art-cate',
        component: () => import('@/views/article/artCate')
      },
      {
        path: 'art-list',
        component: () => import('@/views/article/artList')
      }
    ]
  },
  {
    path: '/reg',
    component: () => import('@/views/register')
    // webpack提供import函数来路由懒加载导入组件
    // 路由懒加载，就是当路径切换到/reg,才会导入相应的组件
    // 好处：让首页加载文件体积更小，打开更快
  },
  {
    path: '/login',
    component: () => import('@/views/login')
  }
]

const router = new VueRouter({
  routes
})
const whiteList = ['/login', '/reg'] // 白名单，无需登录就可以访问的路由地址
// 全局前置路由守卫
// 知识点1：浏览器第一次打开项目页面，会触发一次全局前置路由守卫函数
// 知识点2：判断登录与否，看token，有就是登录了，没有就是未登录
// 知识点3：next()如果强制切换路由地址，还会走路由守卫
router.beforeEach((to, from, next) => {
  console.log(to)
  const token = store.state.token
  if (token) {
    if (!store.state.userInfo.username) { // 当有token的时候,并且没有userInfo的时候去获取用户的信息，防止在登录页发送请求报401错误
      store.dispatch('getUserInfoActions')
    }
    // 已登录，正常放行
    next()
  } else {
    // 未登录
    // 数组.includes(值)，作用：判断值是否在数组中出现过，出现过则返回true
    if (whiteList.includes(to.fullPath)) {
      next()
    } else {
      // 未登录,并且请求的是登录注册以外的页面
      // next()可以中断路由跳转，强制切换到一个路径
      next('/login')
    }
  }
})
export default router
