import Vue from 'vue'
import Vuex from 'vuex'
import createPersistedState from 'vuex-persistedstate'
// 导入axios
import { getUseInforAPI } from '@/api/index'
Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    token: '', // 保存token字符串
    userInfo: {} // 保存用户信息(id,username,nickname,email,user_pic)
  },
  getters: {
  },
  mutations: {
    // 保存token
    updateToken (state, val) {
      state.token = val
    },
    // 保存用户信息
    updateUserInfo (state, val) {
      state.userInfo = val
    }
  },
  actions: {
    // 请求=>用户信息
    async getUserInfoActions (store) {
      const res = await getUseInforAPI()
      console.log(res)
      store.commit('updateUserInfo', res.data.data)
    }
  },
  modules: {
  },
  plugins: [
    createPersistedState() // 注入持久化插件
  ]
})
// vuex默认保存在内存中，所以刷新所有的值会回归初始化（无法做到持久存储）
// 借助vuex-persistedstate@3.2.1 这个包可以实现vuex的持久化存储
// 原理：自动存储到localstorage,不用自己手动写
// 默认存储到localstorage,想存储到sessionStorage，需要配置
