// 封装的是具体的接口请求方法
/* eslint-disable camelcase */

// 每个方法只负责请求一个url地址
import request from '@/utils/request'
// 引入store对象
import store from '@/store/index'
// 导出接口方法，为了在逻辑页面引入后调用
export const registerAPI = ({ username, password, repassword }) => {
  // 原地是一个Promise对象，（内部包含原生ajax请求）
  //   return这个Promise对象到逻辑页面，去那边对Promise对象提取结果
  return request({
    url: '/api/reg',
    method: 'post',
    // get请求用params传参
    // post请求用data传参
    data: {
      username,
      password,
      repassword
    }
  })
}
export const loginAPI = ({ username, password }) => {
  // 原地是一个Promise对象，（内部包含原生ajax请求）
  //   return这个Promise对象到逻辑页面，去那边对Promise对象提取结果
  return request({
    url: '/api/login',
    method: 'post',
    // get请求用params传参
    // post请求用data传参
    data: {
      username,
      password
    }
  })
}
export const getUseInforAPI = () => {
  return request({
    url: '/my/userinfo',
    // get请求用params传参
    // post请求用data传参
    headers: {
      // this.$store.state.token  这里this不是组件对象不能用this.$store拿到store对象
      Authorization: store.state.token
    }
  })
}

export const getMenusListAPI = () => {
  return request({
    url: '/my/menus'
    // headers: {
    // this.$store.state.token  这里this不是组件对象不能用this.$store拿到store对象
    // Authorization: store.state.token
    // }
  })
}
export const updateUserInfotAPI = ({ username, nickname, email }) => {
  return request({
    url: '/my/userinfo',
    method: 'put',
    data: {
      id: store.state.userInfo.id,
      username,
      nickname,
      email
    }
  })
}
// 更新用户头像
export const updateUserPictAPI = (avatar) => {
  return request({
    url: '/my/update/avatar',
    method: 'PATCH',
    data: {
      avatar
    }
  })
}

// 修改用户密码
export const updateUserPwdtAPI = (data) => {
  return request({
    url: '/my/updatepwd',
    method: 'PATCH',
    data
  })
}
// 获取文章分类
export const getArtCateAPI = () => {
  return request({
    url: '/my/cate/list'
  })
}
// 新增文章分类
export const addArtCateAPI = ({ cate_name, cate_alias }) => {
  return request({
    url: '/my/cate/add',
    method: 'post',
    data: {
      cate_name,
      cate_alias
    }
  })
}
// 修改文章分类
export const updateArtCateAPI = ({ id, cate_name, cate_alias }) => {
  return request({
    url: '/my/cate/info',
    method: 'put',
    data: {
      id,
      cate_name,
      cate_alias
    }
  })
}
// 删除文章分类
export const deleteArtCateAPI = (id) => {
  return request({
    url: '/my/cate/del',
    method: 'delete',
    params: {
      id
    }
  })
}
// 发布文章
export const pubArticleAPI = (data) => {
  return request({
    url: '/my/article/add',
    method: 'post',
    data
    // {} 如果是一个普通对象，axios会把它转成JSON字符串在请求体里交给后台
  })
}
// 查询文章
export const getArticleAPI = ({ pagenum, pagesize, cate_id, state }) => {
  return request({
    url: '/my/article/list',
    method: 'get',
    params: {
      pagenum,
      pagesize,
      cate_id,
      state
    }
  })
}
// 查看文章详情
export const articleDetailsAPI = (id) => {
  return request({
    url: '/my/article/info',
    method: 'get',
    params: {
      id
    }
  })
}
// 删除文章
export const deleteArtAPI = (id) => {
  return request({
    url: '/my/article/info',
    method: 'delete',
    params: {
      id
    }
  })
}
